"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useAuth } from "@/contexts/AuthContext"
import Link from "next/link"
import { getSubjects, getChapters, createChapter, deleteChapter, getSemester } from "@/lib/firestore"
import type { Subject, Chapter, Semester } from "@/types"

const initialFormState = {
  subjectId: "",
  title: "",
  difficulty: "Easy" as "Easy" | "Medium" | "Hard",
  estimatedTime: "",
  order: 1,
  quizzes: [] as any[]
}

export default function ChaptersPage({ 
  params 
}: { 
  params: { semesterId: string }
}) {
  const { user } = useAuth()
  const [subjects, setSubjects] = useState<Subject[]>([])
  const [chapters, setChapters] = useState<Chapter[]>([])
  const [semester, setSemester] = useState<Semester | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  // Form state
  const [showForm, setShowForm] = useState(false)
  const [formData, setFormData] = useState(initialFormState)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [semesterData, subjectsData] = await Promise.all([
          getSemester(params.semesterId),
          getSubjects(params.semesterId)
        ])
        setSemester(semesterData)
        setSubjects(subjectsData)

        // Fetch chapters for all subjects
        const allChapters = await Promise.all(
          subjectsData.map(subject => getChapters(subject.id))
        )
        setChapters(allChapters.flat())
      } catch (error) {
        console.error("Error fetching data:", error)
        setError("Failed to load data")
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [params.semesterId])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      await createChapter(formData)
      
      // Refresh chapters for the selected subject
      const updatedChapters = await getChapters(formData.subjectId)
      setChapters(prev => [
        ...prev.filter(ch => ch.subjectId !== formData.subjectId),
        ...updatedChapters
      ])
      
      setShowForm(false)
      setFormData(initialFormState)
    } catch (error) {
      console.error("Error creating chapter:", error)
      setError("Failed to create chapter")
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (chapterId: string, subjectId: string) => {
    if (!confirm("Are you sure? This will delete all quizzes in this chapter.")) {
      return
    }
    setLoading(true)
    try {
      await deleteChapter(chapterId)
      const updatedChapters = await getChapters(subjectId)
      setChapters(prev => [
        ...prev.filter(ch => ch.subjectId !== subjectId),
        ...updatedChapters
      ])
    } catch (error) {
      console.error("Error deleting chapter:", error)
      setError("Failed to delete chapter")
    } finally {
      setLoading(false)
    }
  }

  // Only allow admin access
  if (user?.email !== 'jeniperwalk@gmail.com') {
    return (
      <main className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <h1 className="text-2xl font-bold text-gray-800 mb-4">Access Denied</h1>
          <p className="text-gray-600 mb-4">You do not have permission to access this page.</p>
          <Link href="/">
            <Button className="bg-orange-500">Back to Dashboard</Button>
          </Link>
        </Card>
      </main>
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    )
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <div>
            <Link 
              href="/admin"
              className="text-orange-500 hover:text-orange-600 mb-2 inline-flex items-center gap-2"
            >
              ‚Üê Back to Admin
            </Link>
            <h1 className="text-2xl font-bold text-gray-800">
              Manage Chapters - Semester {semester?.number}
            </h1>
          </div>
          <Button 
            onClick={() => setShowForm(true)}
            className="bg-green-600 hover:bg-green-700"
            disabled={loading}
          >
            Add Chapter
          </Button>
        </div>

        {error && (
          <div className="bg-red-50 text-red-500 p-4 rounded-lg mb-6">
            {error}
          </div>
        )}

        {showForm && (
          <Card className="p-6 mb-8">
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Subject
                </label>
                <select
                  value={formData.subjectId}
                  onChange={(e) => setFormData({ ...formData, subjectId: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                >
                  <option value="">Select a subject</option>
                  {subjects.map(subject => (
                    <option key={subject.id} value={subject.id}>
                      {subject.name} ({subject.code})
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Chapter Title
                </label>
                <input
                  type="text"
                  value={formData.title}
                  onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Difficulty
                </label>
                <select
                  value={formData.difficulty}
                  onChange={(e) => setFormData({ ...formData, difficulty: e.target.value as "Easy" | "Medium" | "Hard" })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                >
                  <option value="Easy">Easy</option>
                  <option value="Medium">Medium</option>
                  <option value="Hard">Hard</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Estimated Time (e.g., "30 mins")
                </label>
                <input
                  type="text"
                  value={formData.estimatedTime}
                  onChange={(e) => setFormData({ ...formData, estimatedTime: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Order
                </label>
                <input
                  type="number"
                  value={formData.order}
                  onChange={(e) => setFormData({ ...formData, order: parseInt(e.target.value) })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  min="1"
                  required
                />
              </div>
              <div className="flex gap-4">
                <Button 
                  type="submit"
                  className="flex-1 bg-orange-500"
                  disabled={loading}
                >
                  Create Chapter
                </Button>
                <Button 
                  type="button"
                  onClick={() => {
                    setShowForm(false)
                    setFormData(initialFormState)
                  }}
                  className="bg-gray-200 text-gray-700 hover:bg-gray-300"
                  disabled={loading}
                >
                  Cancel
                </Button>
              </div>
            </form>
          </Card>
        )}

        <div className="space-y-8">
          {subjects.map((subject) => {
            const subjectChapters = chapters.filter(ch => ch.subjectId === subject.id)
            if (subjectChapters.length === 0) return null

            return (
              <div key={subject.id}>
                <h2 className="text-xl font-semibold text-gray-800 mb-4">
                  {subject.name} ({subject.code})
                </h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {subjectChapters.map((chapter) => (
                    <Card key={chapter.id} className="p-6">
                      <div className="flex justify-between items-start mb-4">
                        <div>
                          <h3 className="text-lg font-semibold text-gray-800">
                            {chapter.title}
                          </h3>
                          <p className="text-gray-600">Difficulty: {chapter.difficulty}</p>
                          <p className="text-gray-600">Time: {chapter.estimatedTime}</p>
                          <p className="text-gray-600">Order: {chapter.order}</p>
                        </div>
                        <Button
                          onClick={() => handleDelete(chapter.id, subject.id)}
                          variant="destructive"
                          size="sm"
                          className="bg-red-500 hover:bg-red-600"
                        >
                          Delete
                        </Button>
                      </div>
                    </Card>
                  ))}
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </main>
  )
}
